:root{
  --Nav-font-size: 1.2em
}

*:focus{
  outline: 1px solid rgb(223, 52, 0);
}
*{
  color:white;
  margin: 0;
  padding: 0;
}
body{
  background-color: #222222;
}
#root{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#Header{
  margin-top: 10px;
  font-size: var(--Nav-font-size);
  box-sizing: border-box;
  padding: 10px;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
}
#HeaderLeft{
  display: flex;
}
#HeaderRight{
  display: flex;
}
#NavHeader-ul{
  list-style: none;
  display: flex;
}
#NavHeader-ul li{margin-right: 10px;}
#NavHeader-ul a{
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#NavHeader-ul a:hover{
  background-color: #1a1a1a;
}
#Logo{
  width: 40px;
  height: 40px;
}
#divImg{
  width: 40px;
  height: 40px;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
}

#Search{
  background-color: #1a1a1a;
  border-radius: 5px;
  position: relative;
  display: block;
  padding: 5px 12px;
  text-decoration: none;
  padding-left: 36px;
}
#Search::after{
  position: absolute;
  top: 4px;
  left: 6px;
  content: " ";
  display: block;
  background: url("./Orion_search-magnify.svg");
  width: 30px;
  height: 30px;
  background-size: 30px 30px;
}
#divAvatar{
  position: relative;
}
#Avatar{
  background: none;
  border: none;
}
#Avatar img{
  width: 35px;
  height: 35px;
}

#AvatarBar{
  z-index: 99;
  font-size: .9em;
  position: absolute;
  top: 120%;
  right: 0;
  background: #1a1a1a;
  padding: 10px;
}
#AvatarBar a, #Login {
  border-radius: 5px;
  padding: 5px 12px;
  text-decoration: none;
  display: block;
}
#AvatarBar a:hover{background-color: #141414;}
#Login{ background-color: #ce5200; }
#Login:hover{ background-color: #b64900; }





#Error {
  width: 100%;
  text-align: center;
  height: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
#Error p{
  font-size: 1.3em;
}
#Error h1{
  font-size: 3em;
}





#MainSlider{
  overflow: hidden;
  margin: 20px 0;
  background-color: #b64900;
  width: 100%;
  user-select: none;
}

#MainSliderContainer{
  transition: .5s;
  position: relative;
  display: flex;
  height: 100%;
}
.MainSliderChild{
  min-width: 100%;
  display: flex;
}
.MainSliderChild img{
  background-color: #141414;
  height: 100%;
}
.MainSliderChild div{
  width: 100%;
  padding: 1%;
  box-sizing: border-box;
}
.MainSliderChild div h2, .MainSliderChild div p{
  margin: 0px;
  padding: 0px;
}
.dot{
  width: 15px;
  height: 15px;
  background: transparent;
  border-radius: 100%;
  border: 1px solid white;
  margin-right: 10px;
}
#Dots{
  position: absolute;
  box-sizing: border-box;
  margin-top: 20px;
  width: 100%;
  text-align: center;
}

.dot:focus{
  background: #a54200;
  outline: none;
}
.dot.focus{
  background: #ff6600;
}


#HotTitles{
  margin-top: 50px;
  display: flex;
  width: 100%;
  height: 207px;
}

#HotTitlesTop{
  position: relative;
  overflow: hidden;
  width: 40%;
  height: 100%;
  background-color: yellow;
}
#HotTitlesSlider{
  overflow: hidden;
  width: 60%;
  height: 100%;
  background-color: red;
}
#HotTitlesTopContainer{
  transition: .3s;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#HotTitlesTopContainer a{
  display: block;
}
.HotTitlesTopChild img{
  transition: .3s;
  display: block;
  width: 100px;
  height: 160px;
  background-color: #141414;
}
.HotTitlesTopChildFocus a:focus, .HotTitlesTopChild a:focus{
  outline: 2px solid rgb(223, 52, 0);
}
.HotTitlesTopChildFocus img{
  transition: .3s;
  display: block;
  width: 130px;
  height: 190px;
  background-color: #141414;
}
.HotTitlesTopDescription{
  box-sizing: border-box;
  background-color: #141414;
  transition: .5s;
  height: 100%;
  padding: 25px;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}
















@media screen and (max-width:500px) {
  #Dots {
    margin-top: 5px;
  }
}
@media screen and (max-width:700px) {
  #MainSlider {
    font-size: .8em;
  }
  #HotTitles {
    display: block;
  }
  #HotTitlesTop{
    min-width: 100%;
  }
  #HotTitlesSlider{
    min-width: 100%;
  }
}
@media screen and (max-width: 1400px) {
  .HotTitlesTopDescription {
    font-size: 1.1em;
  }
}
@media screen and (max-width: 1200px) {
  .HotTitlesTopDescription {
    font-size: 1em;
  }
}
@media screen and (max-width: 992px) {
  .HotTitlesTopDescription {
    font-size: 1em;
  }
}
@media screen and (max-width: 768px) {
  .HotTitlesTopDescription {
    font-size: .9em;
  }
}
@media screen and (max-width: 576px) {
  .HotTitlesTopDescription{
    font-size: .8em;
  }
}








